.table-train {
  border-collapse: collapse;
}

.table-train tbody {
  border: solid;
}

.table-train th, .table-train td {
  height: 1.4em;
  width: 1.4em;
  text-align: center;
  border: thin solid;
  padding: 5px;
}

.example-practice-s1a1 {
  height: 300px;
  width: 300px;
  border: 2px dashed #f0e3e3;
  margin: 0 auto 10px;
  padding: 10px;
  overflow: auto;
}

.inverted-ruby {
  display: inline-block;
  position: relative;
}

.first-table-container th h1 {
  text-align: left;
  color: #185875;
  font-size: 1em;
  font-weight: bold;
}

.first-table-container td {
  font-size: 1em;
  font-weight: normal;
  box-shadow: 0 2px 2px -2px #0e1119;
}

.first-table-container {
  text-align: left;
  width: 80%;
  margin: 0 auto;
  padding: 0 0 8em;
  display: table;
  overflow: hidden;
}

.first-table-container td, .first-table-container th {
  padding-top: 2%;
  padding-bottom: 2%;
  padding-left: 2%;
}

.first-table-container tr:nth-child(2n+1) {
  background-color: #323c50;
}

.first-table-container tr:nth-child(2n) {
  background-color: #2c3446;
}

.first-table-container th {
  background-color: #1f2739;
}

.first-table-container td:first-child {
  color: #fb667a;
}

.first-table-container tr:hover {
  background-color: #464a52;
  box-shadow: 0 6px 6px -6px #0e1119;
}

.first-table-container td:hover {
  color: #403e10;
  transition-property: all;
  transition-duration: .4s;
  transition-delay: 0s;
  transition-timing-function: line;
  background-color: #fff842;
  font-weight: bold;
  transform: translate3d(6px, -6px, 0);
  box-shadow: -1px 1px #7f7c21, -2px 2px #7f7c21, -3px 3px #7f7c21, -4px 4px #7f7c21, -5px 5px #7f7c21, -6px 6px #7f7c21;
}

@media (max-width: 800px) {
  .first-table-container td:nth-child(4), .first-table-container th:nth-child(4) {
    display: none;
  }
}

.second-table-heading {
